import { createStore } from 'vuex'

const store = createStore({
	state: {
		isLoggedIn: true,
		user: null
	},
	mutations: {
		setLoginStatus (state, status) {
			state.isLoggedIn = status
		},
		setUser (state, user) {
			state.user = user
		}
	},
	actions: {
		login ({ commit }, user) {
			// 
			// 
			commit('setLoginStatus', true)
			commit('setUser', user)
		},
		logout ({ commit }) {
			// 
			// 
			commit('setLoginStatus', false)
			commit('setUser', null)
		}
	}
})

export default store;