<template>
  <nut-divider content-position="left"></nut-divider>
  <nut-row>
    <nut-col :span="24">
      <nut-picker v-model="val"
                  :columns="columns"
                  title=""
                  @confirm="confirm"
                  visible-option-num="2" />
    </nut-col>
  </nut-row>
  <nut-input v-model="level"
             placeholder="Number"
             type="number"
             input-align="right"
             @update:model-value="updateLevel">
    <template #left>
      :
    </template>
  </nut-input>
  <nut-row>
    <nut-col :span="8">
      <div class="content fontCoarse">
      </div>
    </nut-col>
    <nut-col :span="8">
      <div class="content fontCoarse">
      </div>
    </nut-col>
    <nut-col :span="8">
      <div class="content fontCoarse">
      </div>
    </nut-col>
  </nut-row>
  <nut-row>
    <nut-col :span="8">
      <div class="content fontCoarse">{{ ttgConvert.Down15 }}{{initTtgConvert.Down15}}</div>
    </nut-col>
    <nut-col :span="8">
      <div class="content fontCoarse">1.5
      </div>
    </nut-col>
    <nut-col :span="8">
      <div class="content fontCoarse">{{ ttgConvert.Up15 }}{{initTtgConvert.Up15}}
      </div>
    </nut-col>
  </nut-row>
  <nut-row>
    <nut-col :span="8">
      <div class="content fontCoarse">{{ ttgConvert.Down25 }}{{initTtgConvert.Down25}}
      </div>
    </nut-col>
    <nut-col :span="8">
      <div class="content fontCoarse">2.5
      </div>
    </nut-col>
    <nut-col :span="8">
      <div class="content fontCoarse">{{ ttgConvert.Up25 }}{{initTtgConvert.Up25}}
      </div>
    </nut-col>
  </nut-row>
  <nut-row>
    <nut-col :span="8">
      <div class="content fontCoarse">{{ ttgConvert.Down35 }}{{initTtgConvert.Down35}}
      </div>
    </nut-col>
    <nut-col :span="8">
      <div class="content fontCoarse">3.5
      </div>
    </nut-col>
    <nut-col :span="8">
      <div class="content fontCoarse">{{ ttgConvert.Up35 }}{{initTtgConvert.Up35}}
      </div>
    </nut-col>
  </nut-row>
  <nut-row>
    <nut-col :span="8">
      <div class="content fontCoarse">{{ ttgConvert.Down45 }}{{initTtgConvert.Down45}}
      </div>
    </nut-col>
    <nut-col :span="8">
      <div class="content fontCoarse">4.5
      </div>
    </nut-col>
    <nut-col :span="8">
      <div class="content fontCoarse">{{ ttgConvert.Up45 }}{{initTtgConvert.Up45}}
      </div>
    </nut-col>
  </nut-row>
</template>

<script setup>
import { h, ref } from 'vue';
import { getCrsToTtgConvert } from './calculation.js'
const crsList = ref([]);
const ttgConvert = ref({});
const initTtgConvert = ref({});
const level = ref(1.88);
const obj = ref({});
const pushCrsList = (list) => {
  crsList.value = list;
  initTtgConvert.value = getCrsToTtgConvert(list[0], level.value);
  ttgConvert.value = getCrsToTtgConvert(list[list.length - 1], level.value);
  let columnsT = [];
  let name;
  let ttg;
  for (let i = 0; i < list.length; i++) {
    ttg = list[i];
    name = list[i].updateDate + "  " + list[i].updateTime
    columnsT.push({ text: name, value: name, obj: list[i] });
  }
  obj.value = ttg;
  columns.value = columnsT;
  val.value = [name];
}

const val = ref('')
const columns = ref([])
const confirm = ({ selectedValue, selectedOptions }) => {
  try {
    ttgConvert.value = getCrsToTtgConvert(selectedOptions[0].obj, level.value);
    obj.value = selectedOptions[0].obj;
  } catch (e) {

  }
}

const updateLevel = (val) => {
  try {
    ttgConvert.value = getCrsToTtgConvert(obj.value, val);
  } catch (e) {

  }
}

// 
defineExpose({
  pushCrsList
});
</script>

<style scoped>
.u-img {
  width: 11px;
  display: inline-block;
}
.u-batNum {
  padding: 0 2px;
}
</style>